* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html {
  /* color: white; */
}

::-moz-selection {
  background-color: white;
  color: #151515;
}

::selection {
  background-color: white;
  color: #151515;
}

:root {
  --panelHeight: 70px;
  --panelFontColor: white;
  --panelMainColor: #151515;
  --panelSecondColor: #444444;
}

div.panel {
  -webkit-flex-basis: 70px;
          flex-basis: 70px;
  -webkit-flex-basis: var(--panelHeight);
          flex-basis: var(--panelHeight);
  background-color: #151515;
  background-color: var(--panelMainColor);
  color: white;
  color: var(--panelFontColor);
  font-family: "Oswald", sans-serif;
  line-height: 70px;
  line-height: var(--panelHeight);
  font-size: calc(70px / 3);
  font-size: calc(var(--panelHeight) / 3);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0 0 10px #151515;
  box-shadow: 0 0 10px var(--panelMainColor);
  border-bottom: 1px solid #444444;
  border-bottom: 1px solid var(--panelSecondColor);
}

div.panel h1 {
  padding-right: 30px;
  -webkit-flex-basis: 20%;
          flex-basis: 20%;
  min-width: 300px;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  text-align: right;
  line-height: 70px;
  line-height: var(--panelHeight);
  font-size: calc(70px / 3);
  font-size: calc(var(--panelHeight) / 3);
  color: white;
  color: var(--panelFontColor);
  font-weight: bold;
  letter-spacing: 2px;
  text-transform: uppercase;
}

i.fa-bars,
i.fa-times {
  position: fixed;
  top: 20px;
  left: 20px;
  line-height: calc(70px / 2);
  line-height: calc(var(--panelHeight) / 2);
  width: calc(70px / 2);
  width: calc(var(--panelHeight) / 2);
  font-size: calc(70px / 2);
  font-size: calc(var(--panelHeight) / 2);
  height: calc(70px / 2);
  height: calc(var(--panelHeight) / 2);
  color: white;
  color: var(--panelFontColor);
  padding-left: 20px;
  display: none;
  cursor: pointer;
}

input.priority {
  -webkit-flex-basis: 20px;
          flex-basis: 20px;
  height: 20px;
  margin: 0 10px;
}

input.make {
  height: 70px;
  height: var(--panelHeight);
  padding: 10px;
  width: 0;
  -webkit-flex-basis: 200px;
          flex-basis: 200px;
  border: none;
  border-left: 1px solid #444444;
  border-left: 1px solid var(--panelSecondColor);
  border-right: 1px solid #444444;
  border-right: 1px solid var(--panelSecondColor);
  background-color: #444444;
  background-color: var(--panelSecondColor);
  color: white;
  outline: none;
  font-size: calc(70px / 2);
  font-size: calc(var(--panelHeight) / 2);
  font-family: "Oswald", sans-serif;
}

div.panel button {
  -webkit-flex-basis: 20%;
          flex-basis: 20%;
  border: none;
  border-bottom: 1px solid #444444;
  border-bottom: 1px solid var(--panelSecondColor);
  outline: none;
  font-family: "Oswald", sans-serif;
  height: 70px;
  height: var(--panelHeight);
  background-color: #151515;
  background-color: var(--panelMainColor);
  cursor: pointer;
  transition: 0.2s;
  font-size: calc(70px / 3);
  font-size: calc(var(--panelHeight) / 3);
  position: relative;
  color: white;
  color: var(--panelFontColor);
  letter-spacing: 2px;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
}

div.panel button:hover {
  opacity: 0.2;
}

@media (max-width: 1024px) {
  div.panel {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 2;
    width: 70vw;
    height: 100vh;
    -webkit-flex-direction: column;
            flex-direction: column;
    border-left: 1px solid crimson;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    transition: 0.2s;
  }

  div.panel.active {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  div.panel h1,
  input.make {
    -webkit-flex-basis: 70px;
            flex-basis: 70px;
    -webkit-flex-basis: var(--panelHeight);
            flex-basis: var(--panelHeight);
    -webkit-flex-grow: 0;
            flex-grow: 0;
  }
  div.panel h1 {
    width: 100%;
    margin: 0;
    padding: 0;
    text-align: center;
    font-size: 20px;
  }

  input.make {
    width: 50vw;
  }

  i.fa-bars,
  i.fa-times {
    display: block;
    z-index: 3;
  }

  div.panel button {
    position: absolute;
    -webkit-flex-basis: 70px;
            flex-basis: 70px;
    -webkit-flex-basis: var(--panelHeight);
            flex-basis: var(--panelHeight);
    border: 2px solid #444444;
    border: 2px solid var(--panelSecondColor);
    width: 50vw;
    min-height: 70px;
    min-height: var(--panelHeight);
    bottom: 10px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
}

:root {
  --h1Size: 25px;
  --taskSize: 60px;
}

div.display {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  color: white;
  position: relative;
}

div.display > h1 {
  -webkit-flex-basis: 30px;
          flex-basis: 30px;
  line-height: 30px;
  font-size: 20px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-family: "Oswald", sans-serif;
  font-weight: 100;
  color: white;
  position: relative;
  margin-top: 50px;
  font-weight: bold;
}

div.display li {
  height: calc(60px + 20px);
  height: calc(var(--taskSize) + 20px);
  margin: 10px 20vw;
  min-width: 250px;
  border-bottom: 1px solid #151515;
  border-radius: 40px;
  background-color: #444444;
  position: relative;
}

h1.task__h1,
h2.task__h2 {
  text-align: center;
  font-family: "Oswald", sans-serif;
  font-size: 25px;
  font-size: var(--h1Size);
  letter-spacing: 2px;
}
li button {
  position: absolute;
  background-color: #151515;
  border: none;
  height: 60px;
  height: var(--taskSize);
  font-family: "Oswald", sans-serif;
  font-weight: bold;
  color: white;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  border-radius: 50%;
  left: 10px;
  width: 60px;
  width: var(--taskSize);
  line-height: 60px;
  line-height: var(--taskSize);
  font-size: calc(60px / 2);
  font-size: calc(var(--taskSize) / 2);
  outline: none;
  cursor: pointer;
  transition: 0.2s;
}

li button:hover {
  opacity: 0.1;
}

li.priority h1,
li.priority h2,
li.priority button {
  color: crimson;
}

div.display input {
  position: relative;
  width: 200px;
  height: 30px;
  font-size: 25px;
  line-height: 30px;
  padding-bottom: 5px;
  background-color: var(--panelMainColor);
  color: white;
  outline: none;

  font-family: "Oswald", sans-serif;
  border: none;
  border-bottom: 1px solid crimson;
}

div.display label {
  position: absolute;
  top: 30px;
  left: 5%;
}

i.fa-search {
  position: absolute;
  top: 0;
  height: 30px;
  width: 30px;
  text-align: center;
  left: 0;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  color: crimson;
  font-size: 20px;
  line-height: 30px;
  background-color: var(--panelSecondColor);
}

div.display ul {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  list-style: none;
}

@media (max-width: 1024px) {
  :root {
    --h1Size: 16px;
    --taskSize: 40px;
  }

  div.display label {
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    top: 100px;
  }

  div.display ul {
    margin-top: 100px;
  }

  h1.task__h1,
  h2.task__h2 {
    letter-spacing: 1px;
  }
  div.display li {
    min-width: 150px;
    height: calc(60px + 20px);
    height: calc(var(--taskSize) + 20px);
    margin: 10px 10vw;

    border-radius: calc(5vh + 10px);
  }
}

div.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  background-color: #151515;
}

